<style scoped lang="less">
  @import "./list.less";
</style>

<template>
  <div class="list">
  	<Row :gutter="20">
     	<Col :sm="8" :md="6" :lg="4"><Input v-model="queryTitle" placeholder="请输入文章标题"></Input></Col>
     	<Col :sm="7" :md="5" :lg="3">
   			<Select v-model="queryType" placeholder="请选择文章类型">
   		    <Option v-for="item in optionList" :value="item.value" :key="item.value">{{ item.label }}</Option>
   		  </Select>
     	</Col>
     	<Col :sm="8" :md="6" :lg="5">
        <Button type="primary" icon="search" shape="circle" style="margin-right:10px;">搜索</Button>
        <Button type="primary" icon="plus" shape="circle" @click="add">新增</Button>
        <Modal
	        v-model="articleModal"
	        title="新增文章"
	        @on-ok="addOrEdit"
	        @on-cancel="tagModal=false">
	        <p>Content of dialog</p>
 		    </Modal>
      </Col>
    </Row>
    <baseTable
      @on-edit="edit" 
			@on-delete="remove" 
			@on-page-change="onCurrentPageChange"
			@on-size-change="onPageSizeChange"
			:columnList="tableColumn" 
			:total="total"
			:currentPage="currentPage"
			:pageSize="pageSize"
			:loading="tableLoading"
			v-model="tableData"></baseTable>
  </div>
</template>

<script>
import allTableColumn from '@/common/js/table-column'
import baseTable from '@/components/table/base-table'
export default {
  name: 'list',
  data () {
    return {
      queryTitle: '',
      queryType: '',
			articleModal: false,
			tableLoading: false,
			currentPage: 1,
			pageSize: 10,
			total: 0,
		  tableData: [
		    {
		      index: '1',
		      type: '技术',
		      title: 'test',
          createTime: '2018-02-08 16:18'
		    }
		  ],
		  optionList: [
		  {
        value: '1',
        label: '技术'
      },{
        value: '2',
        label: '生活'
      },{
        value: '3',
        label: '足球'
      }
	    ]
	  }
	},
	created(){
		this.tableColumn = allTableColumn.articleTableColumn
	},
	mounted(){},
	methods:{
    onCurrentPageChange(page){},
		onPageSizeChange(size){},
		// 新增
    add(){
    	this.articleModal = true
		},
		edit(row, index){
      this.articleModal = true
    },
    addOrEdit(){
      this.$Message.info('Clicked ok');
		},
		// info(row){
		// 	console.log(row)
		// },
		remove(row, index){
      this.tableData.splice(index, 1);
    },
  },
  components: {
    baseTable
  }
}
</script>


